import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import './style.css';
var name;
const contacts=['AB','BC','CD'];

class Add extends React.Component{
  constructor(props)
  {
    super(props);
    this.state={ person : ''};
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(e)
  {
   this.setState({person : e.target.value});
  }
  handleSubmit(e)
  {
    if(this.state.person !== '') {
      this.props.handleSubmit(this.state.person);
      this.setState({person : ''});
    }
    e.preventDefault();
  }
  
  render(){
   
    return(
       <form className='form' onSubmit={this.handleSubmit}>
         <input type='text' placeholder='enter details' value={this.state.person} onChange={this.handleChange} />
         <button type='submit'>ADD</button>
       </form>
    );
  }

}
class Display extends React.Component{
  render()
  {  const arr=this.props.dat;
    const listItems=arr.map((con,index)=><li key={index}>{con}</li> );
    return(
      <ul>
        {listItems}
      </ul>
    );
  }
}
class Manager extends React.Component{
  constructor(props){
    super(props);
    this.state={val:this.props.data};
    this.addCont = this.addCont.bind(this);

  }
  addCont(name) {
    const a=this.state.val.slice();
    a.push(name);
    this.setState({val:a});
  }
  
  render(){
    
    return(
      <div>
       <Add handleSubmit={this.addCont} />
       <Display dat={this.state.val} />
       </div>
    );
  }

}
const el=<div><Manager data={contacts} /></div>;
ReactDOM.render(
    el , document.getElementById('root')
);